.prices{
  background-color: $blockBg;
  margin-bottom: 1.5em;
  &__inner{
    padding: 1em 0 6.25em;
  }
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
  }
  .submenu__types-grid{
    display: grid;
    grid-template-columns: repeat(7,1fr);
    gap: 1em;
    margin-top: 3em;
  }
}

.prices-service{
  padding: 3.125em 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 27.5em) 24.5em;
    gap: 3em;
  }
  &__items{
    margin-top: 2.5em;
  }
  &__prices{
    display: grid;
    grid-template-columns: 100%;
    gap: .625em;
    &-item{
      font-size: 1em;
      background: $blockBg;
      border-radius: .75em;
      padding: 1.5em;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  &__right{
    padding-top: 4em;
  }

}

.price-consult{
  height: unset;
  padding: 2.5em 8.75em 2.5em 2.5em;
  .doctor-consult{
    &__nav{
      margin-top: 1.5em;
    }
    &__img{
      width: 8.0625em;
    }
  }
}

.feedback{
  padding: 3.125em 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 27.5em) 24.5em;
    gap: 3em;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 1em;
    margin-top: 2.5em;
  }
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.875em;
    margin-top: 1.25em;
  }
  &__links{
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
  }
  &__link{
    color: $linkBlue;
    &:hover{
      color: $black;
    }
  }
}

.feedback-card{
  padding: 2.5em;
  background-color: $blockBg;
  border-radius: .75em;
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: .625em;
  }
  &__nav{
    margin-top: 1.25em;
    display: flex;
  }
}

.price{
  &__inner{
    padding: 1em 0 1.5em;
  }
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
    margin-top: 2.5em;
  }
}

.price-info{
  &__groups{
    padding-top: 1em;
    display: grid;
    grid-template-columns: 100%;
    gap: 3em;
  }
  &__subtitle{
    margin-bottom: .35em;
    &:last-child{
      margin-bottom: 0;
    }
  }
  &__items{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
    margin-top: 1.25em;
  }
  @media screen and (min-width: 577px){
    .how-cheaper,
    .doc-link{
      padding: 0;
      margin-top: 4.75em;
      .swiper{
        &-wrapper{
          grid-template-columns: 100%;
          gap: 2.5em;
        }
      }
      .display2{
        font-size: 1.75em;
        line-height: 122%;
      }
      .container{
        padding: 0;
      }
    }
  }
}

.price-tariff{
  padding: 1.5em 0 3.125em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc((100% / 3) - 2em));
    gap: 3em;
  }
}

.tariff-card2{
  padding: 2.5em;
  background: rgba(255, 255, 255, 0.9);
  border: 1px solid $lightGray;
  border-radius: .75em;
  &__inner{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.875em;
  }
  &__items,
  &__sum{
    display: grid;
    grid-template-columns: 100%;
    gap: .625em;
  }
  &__tabs{
    .tab2{
      padding: 0 .75em;
      height: 2.0625em;
      &:not(._active){
        background-color: $blockBg;
        border-color: $blockBg;
        &:hover{
          background-color: #F4F0E9;
          color: rgba($black,.8);
        }
      }
      span{
        font-size: .75em;
      }
      &:not(:last-child){
        margin-right: .625em;
      }
    }
  }
  .prices-service__prices-item{
    padding: .8125em 1.25em;
  }
  &__prices{
    margin-top: 1.25em;
  }
  &__sum{
    display: grid;
    grid-template-columns: 100%;
    gap: .625em;
  }
  &__btn{
    height: 3.4em;
    width: 100%;
    img{
      width: 1.2em;
      display: block;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
}

.price-info2{
  padding-top: 1.5em;
  .prices-service__right{
    padding-top: 0;
  }
  .price-info__items{
    gap: .875em;
  }
  .prices-service__prices-item{
    padding: .8125em 1.25em;
  }
  &__subtitle{
    margin: 2.5em 0 1.25em;
  }
  .price-info__groups{
    padding-top: 0;
  }
  .spoiler-card{
    padding-left: 1.25em;
    &:not(:first-child){
      border-top: none;
    }
  }
}

.price-sale{
  &__badge{
    display: inline-flex;
    align-items: center;
    padding: 0 .625em;
    background: #ECDFF3;
    border-radius: .5em;
    height: 2em;
    color: $primary;
    margin-top: .625em;
  }
}


@media screen and (max-width: 576px){
  .prices{
    margin-bottom: 1.875em;
    &__inner{
      padding-bottom: 3.75em;
    }
    .submenu__types-grid{
      grid-template-columns: repeat(2,1fr);
      gap: .75em;
      margin-top: 2.25em;
    }
    .type-card{
      min-height: 6.25em;
    }
  }

  .prices-service{
    padding: 1.875em 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 3.75em;
    }
    &__right{
      order: -1;
      padding: 0;
    }
    &__items{
      margin-top: 1.5em;
    }
    &__prices{
      gap: .875em;
      &-item{
        padding: .875em 1.25em;
      }
    }

  }

  .price-consult{
    height: unset;
    padding: 1.5em 4.6875em 1.5em 1.5em;
    .doctor-consult{
      &__img{
        width: 6.125em;
      }
    }
  }

  .feedback{
    padding: 1.875em 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 6em;
    }
    &__items{
      grid-template-columns: 100%;
      gap: .75em;
      margin-top: 1.5em;
    }
    &__info{
      margin-top: .625em;
    }
  }

  .feedback-card{
    padding: 1.5em;
    &__info{
      gap: .375em;
    }
    &__nav{
      margin-top: .875em;
    }
  }

  .price-info{
    .prices-service__right{
      order: unset;
    }
    .how-cheaper{
      width: calc(100% + 3em);
      margin: 1.875em 0 1.875em -1.5em;
    }
    &__items{
      gap: .75em;
    }
    .doc-link{
      margin-top: 3.75em;
    }
  }

  .price-tariff{
    padding-bottom: 1.875em;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }

  .tariff-card2{
    padding: .875em;
    &__inner{
      gap: 1.25em;
    }
    &__btn{
      font-size: 1em;
      height: 2.5em;
      border-radius: .5em;
      span{
        font-size: .875em;
      }
      img{
        width: 1.125em;
        margin-right: .5em;
      }
    }
  }

  .price-info2{
    .price-info__items{
      gap: .75em;
    }
  }

  .price-sale{
    padding-bottom: 3.125em !important;
    position: relative;
    &__badge{
      position: absolute;
      left: 1.25em;
      bottom: .8125em;
      height: 1.625em;
    }
  }
}
