@import "../mixins";
.about{
  background-image: none;
  .tv__info{
    max-width: 38.5625em;
    margin-top: 3em;
  }
  .tv__nav{
    margin-top: 3em;
  }
  .consult-page__left{
    padding-bottom: 4.75em;
  }
}

.trust{
  padding: 6.25em 0;
  .rating{
    padding: 0;
    margin-top: 4.75em;
    .display2{
      font-size: 1.75em;
    }
  }
  .reward{
    padding: 0;
    margin-top: 6.25em;
    .display2{
      font-size: 1.75em;
    }
    &__title,
    &__desc{
      text-align: left;
    }
    &__nav{
      display: none;
    }
  }
}

.bracket-info{
  padding: 6.25em 0 0;
  background-color: $blockBg;
  &__grid{
    margin-top: 3em;
    @include grid348;
  }
}

.bracket-card{
  &__inner{
    @include card;
    background-color: #fff;
    background-repeat: no-repeat;
    background-position: right top;
    background-size: 8.25em auto;
  }
  &__info{
    @include grid100gap(3.75em);
  }
}

.media-about{
  padding-bottom: 6.25em;
}

.team2{
  padding: 6.25em 0;
  &__inner{
    padding: 22.5em 2.5em 2.5em;
    background-color: $black;
    color: $lightGray;
    position: relative;
    border-radius: .75em;
    overflow: hidden;
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    &:before{
      content: '';
      position: absolute;
      background: linear-gradient(180deg, rgba(128, 120, 122, 0) 0%, #80787A 100%);
      top: 0;
      left: 0;
      z-index: 2;
      width: 100%;
      height: 100%;
      border-radius: .75em;
    }
    img{
      border-radius: .75em .75em 0 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__content{
    max-width: 50.625em;
    position: relative;
    z-index: 3;
    @include grid100gap(.625em);
  }
  &__desc{
    @include grid100gap(.625em);
    p{
      line-height: inherit;
    }
  }
  &__nav{
    display: flex;
    margin-top: 1.875em;
  }
  &__link{
    &:hover{
      border-color: #fff;
    }
  }
}

.edu-program{
  padding: 3.125em 0;
  &__top{
    @include grid100gap(1.25em);
  }
  &__grid{
    margin-top: 3em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - (3.75em / 4)));
    gap: .75em 1.25em;
  }
}

.edu-card2{
  @include card;
  background-color: $blockBg;
  display: flex;
  flex-direction: column;
  &__info{
    @include grid100gap(.625em);
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.25em;
    display: flex;
  }
}

@media screen and (max-width: 576px){
  .about{
    padding: 2.1875em 0 3.75em;
    position: relative;
    overflow: hidden;
    .consult-page__left{
      padding: 0;
      position: relative;
      z-index: 2;
    }
    .tv__info,
    .tv__nav{
      margin-top: 1.5em;
    }
    .consult-page__right{
      display: block;
      position: absolute;
      width: 15.19em;
      height: 16.125em;
      bottom: -1em;
      right: -2.5em;
    }
  }

  .trust{
    padding: 3.75em 0;
    .rating,
    .reward{
      margin-top: 3em;
      .display2{
        font-size: 1.25em;
      }
    }
  }

  .bracket-info{
    padding: 3.75em 0 0;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.5em;
      margin-top: 1.5em;
    }
  }

  .bracket-card{
    &__inner{
      padding: 1.5em;
      background-size: 7em auto;
    }
    &__info{
      gap: 1.25em;
    }
  }

  .media-about{
    padding-bottom: 3.75em;
  }

  .team2{
    padding: 3.75em 0 1.875em;
    &__inner{
      padding: 0;
    }
    &__img{
      position: relative;
      height: unset;
      &:before{
        border-radius: .75em .75em 0 0;
      }
    }
    &__content{
      padding: 0 1.5em 1.5em;
      margin-top: -2em;
      gap: .5em;
    }
    &__nav{
      margin-top: .75em;
    }
  }

  .edu-program{
    padding: 1.875em 0;
    &__top{
      gap: .625em;
    }
    &__grid{
      margin-top: 1.5em;
      grid-template-columns: 100%;
    }
  }

  .edu-card2{
    padding: 1.5em;
    &__info{
      gap: .375em;
    }
    &__nav{
      padding-top: .875em;
    }
  }
}
