@import "../mixins";

.course{
  &__inner{
    padding: 1em 0 3.125em;
  }
  &__info{
    @include grid100;
    gap: 1.25em;
    max-width: 48.5em;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5em));
    gap: 1.5em 3em;
    margin-top: 3em;
  }
  &__nav{
    display: flex;
    margin-top: 6.25em;
  }
  &__btn{
    padding: 0 1.55em;
    img{
      width: 1.2em;
      display: block;
      flex-shrink: 0;
      margin-right: .5em;
    }
    ._mob-view{
      display: none;
    }
  }
}

.course2{
  position: relative;
  .container{
    position: relative;
    z-index: 2;
  }
  &__bg{
    background-size: cover;
    background-position: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  &__inner{
    padding: 21.75em 0 4.75em;
  }
  &__info{
    @include grid100;
    gap: 1.25em;
    max-width: 48.5em;
  }
}

.course3{
  padding: 4.75em 0 3.125em;
  .course__grid{
    margin-top: 0;
  }
}


@media screen and (max-width: 576px){
  .course{
    &__inner{
      padding-bottom: 1.875em;
    }
    &__info{
      gap: .625em;
    }
    &__grid{
      margin-top: .85em;
      grid-template-columns: 100%;
      gap: .85em;
    }
    &__nav{
      margin-top: 2.25em;
    }
    &__btn{
      width: 100%;
      ._mob-view{
        display: unset;
      }
      ._desktop-view{
        display: none;
      }
    }
  }

  .course2{
    &__bg{
      position: static;
      padding-bottom: 56%;
    }
    &__inner{
      padding: 0;
      margin-top: -3.125em;
    }
    &__info{
      gap: .625em;
    }
  }

  .course3{
    padding: .75em 0 1.875em;
  }
}
