@import "../mixins";
.files{
  padding: 3.125em 0;
  &__grid{
    @include grid348;
    margin-top: 3em;
  }
}

.file-card{
  padding: 1em 1.25em;
  background-color: $blockBg;
  border-radius: .5em;
  display: flex;
  &__img{
    width: 2.25em;
    flex-shrink: 0;
    margin-right: 1.25em;
    img{
      width: 100%;
    }
  }
  &__info{
    @include grid100gap(.25em);
  }
}


@media screen and (max-width: 576px){
  .files{
    padding: 1.875em 0;
    &__grid{
      margin-top: 1.25em;
      gap: .75em;
      grid-template-columns: 100%;
    }
  }

  .file-card{
    &__img{
      width: 1.875em;
      margin-right: .625em;
    }
    &__info{
      gap: 0;
    }
  }
}
