@import "../mixins";
.vacancies{
  &__inner{
    padding: 1em 0 3.125em;
  }
  &__grid{
    @include grid348;
    margin-top: 3em;
  }
}

.vacancy-card{
  @include card;
  display: flex;
  flex-direction: column;
  &__top{
    @include grid100gap(1.25em);
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1.875em;
    @include grid100gap(1.25em);
  }
  &__price{
    @include grid100gap(.1875em);
  }
  &__nav{
    display: flex;
  }
  &__btn{
    &:not(:last-child){
      margin-right: 1.42em;
    }
  }
}

.vacancy{
  background-color: $blockBg;
  &__inner{
    padding: 1em 0 4.75em;
  }
  &__info{
    margin-top: 3em;
    @include grid100gap(1.25em);
    max-width: 48.5em;
  }
  &__desc{
    @include grid100gap(1em);
    p{
      line-height: inherit;
    }
  }
  &__price{
    @include grid100gap(.1875em);
    margin-top: 3em;
  }
  &__nav{
    display: flex;
    margin-top: 3em;
  }
}

.vacancy-info{
  padding: 4.75em 0 3.125em;
  &__inner{
    display: grid;
    grid-template-columns: auto 24.6875em;
    gap: 3.75em 3em;
  }
  &__left{
    max-width: 48.5em;
  }
  .content-text{
    ul{
      gap: 1em;
    }
    hr{
      margin-bottom: 4.75em;
    }
  }
}

.vacancy-stages{
  @include card;
  background-color: $blockBg;
  &__items{
    @include grid100gap(2.5em);
    margin-top: 1.5em;
  }
  &__item{
    @include grid100gap(.625em);
  }
}

.vacancy-more{
  @include grid100gap(1.5em);
  margin-top: 3em;
  &__nav{
    margin-top: 1.5em;
    display: flex;
  }
  &__btn{
    width: 100%;
    height: 3.4em;
  }
}


@media screen and (max-width: 576px){
  .vacancies{
    &__inner{
      padding-bottom: 1.875em;
    }
    &__grid{
      margin-top: 2.25em;
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }

  .vacancy-card{
    padding: 1.5em;
    &__top{
      gap: .5em;
    }
    &__bottom{
      padding-top: .625em;
    }
  }

  .vacancy{
    &__info{
      margin-top: 1.5em;
    }
    &__price,
    &__nav{
      margin-top: 2.125em;
    }
  }

  .vacancy-info{
    padding: 3.75em 0 1.875em;
    &__inner{
      grid-template-columns: 100%;
    }
    .content-text{
      hr{
        margin-bottom: 2.6em;
      }
    }
  }

  .vacancy-stages{
    padding: 1.5em;
    &__items{
      gap: 2em;
    }
    &__item{
      gap: .25em;
    }
    .content-text{
      margin-top: .4em;
      ul{
        gap: 0;
      }
    }
  }

  .vacancy-more{
    margin-top: 3.75em;
    gap: .75em;
  }
}
