.service{
  margin-bottom: 1.5em;
  background-color: $blockBg;
  overflow: hidden;
  &__inner{
    display: grid;
    grid-template-columns: auto 33.3125em;
    gap: 3em;
    padding: 1em 0 6.25em;
    position: relative;
  }
  &__left{
    flex-grow: 1;
  }
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
  }
  &__list{
    gap: .5em;
    margin-top: 2.22em;
  }
  &__img{
    width: 33.3125em;
    display: block;
    position: absolute;
    bottom: 0;
    right: 0;
  }
  &__img2{
    display: none;
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
  }
  &__btn{
    height: 3.4em;
  }
}

.service-article{
  padding: 3.125em 0;
  .content-text{
    margin: 0;
    max-width: 48.5em;
    hr{
      margin: 1.25em 0;
    }
    &>ul{
      gap: 1em;
    }
    &>blockquote{
      margin-top: 5.55em;
    }
  }
}

.universities{
  display: flex;
  flex-wrap: wrap;
  padding-left: .875em;
}

.university-card{
  padding: 1.5em .875em;
  background-color: $blockBg;
  text-align: center;
  border-radius: .75em;
  width: 9.75em;
  max-width: 100%;
  &:not(:last-child){
    margin-right: 1.25em;
  }
  &__img{
    height: 2.125em;
    display: flex;
    justify-content: center;
    margin-top: .75em;
    img{
      height: 100%;
      width: auto;
    }
  }
}

.best{
  padding: 3.125em 0;
  &__top{
    max-width: 48.5em;
  }
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5em));
    gap: 3em;
  }
}

.best-card{
  padding: 2.5em;
  background: rgba(255, 255, 255, 0.9);
  border: 1px solid $lightGray;
  border-radius: .75em;
  display: flex;
  flex-direction: column;
  &__top{
    @include grid100;
    gap: 1.25em;
  }
  &__desc{
    @include grid100;
    gap: 1em;
    p{
      line-height: inherit;
    }
  }
  &__images{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: .625em;
    align-items: center;
    img{
      width: 100%;
    }
  }
  &__text{
    margin-top: 2.5em;
    ul{
      li{
        &:before{
          background-color: transparent;
          background-size: 100% auto;
          background-image: url(../img/check4.svg);
          width: 1.43em;
          height: 1.43em;
          margin-right: .57em;
        }
      }
    }
  }
  &__text2{
    margin-top: auto;
    padding-top: 2.5em;
  }
  &__logo{
    width: 12.5em;
    img{
      width: 100%;
    }
  }
}

.intellectual{
  padding: 6.25em 0;
  margin-top: 3.125em;
  background-color: $blockBg;
  &__inner{
    display: grid;
    grid-template-columns: auto 24.6875em;
    gap: 1.5em 3em;
  }
  &__left{
    max-width: 48.5em;
  }
  &__subtitle{
    margin-top: 2.5em;
    @include grid100;
    gap: .5em;
  }
  &__desc{
    margin-top: 1.25em;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
    margin-top: 2.5em;
  }
  .when-need__right{
    background-image:url(../img/pattern.svg), radial-gradient(62.23% 670.3% at 91.8% 13.33%, #9E9396 0%, #80787A 100%);
    background-size: cover;
    box-shadow: 0px 24px 50px rgba(119, 110, 112, 0.5);
    color: $lightGray;
  }
  .tv-med2__step-num{
    color: $primary;
    background: #F2E8F9;
  }
}

.intellectual-card{
  background: #FFFFFF;
  border-radius: .75em;
  padding: 2.5em;
  &__top{
    display: flex;
    align-items: center;
  }
  &__num{
    width: 2.8125em;
    height: 2.8125em;
    position: relative;
    flex-shrink: 0;
    margin-right: 1.25em;
    svg{
      width: 100%;
      height: 100%;
    }
    &-text{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
    }
  }
  &__text{
    margin-top: 1.875em;
    @include grid100;
    gap: 1.25em;
  }
}

.req2{
  padding: 2.71875em 0;
  position: relative;
  margin-bottom: 1em;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .req{
    position: relative;
    z-index: 2;
    background: rgba(245, 244, 241, 0.8);
    backdrop-filter: blur(12px);
    width: 36.875em;
    max-width: 100%;
    margin-left: auto;
    margin-top: 0;
    &__hint{
      max-width: 17.75em;
    }
  }
}

.m-slider{
  &__top{
    @include grid100gap(1.25em);
  }
  &__slider{
    .swiper{
      @media screen and (min-width: 577px){
        &-wrapper{
          grid-template-columns: repeat(3,calc((100% / 3) - 2em));
          gap: 3em;
        }
        &-slide{
          width: 100% !important;
        }
      }
    }
  }
}

.algorithm{
  padding: 3.125em 0 6.25em;
}

.algorithm-card{
  background: rgba(255, 255, 255, 0.9);
  border-radius: .75em;
  padding: 2.5em;
  height: 100%;
  &__top{
    @include grid100gap(.625em);
  }
  &__badge{
    display: inline-flex;
    align-items: center;
    margin: 1.25em 0;
    padding: 0 .75em;
    height: 2.1875em;
    border-radius: .375em;
    border: 1px solid $gray;
  }
  &__what-do{
    @include grid100gap(.25em);
  }
  &__nav{
    display: flex;
    margin-top: 1.25em;
  }
}

.othodontist{
  padding: 6.25em 0;
  background-color: $blockBg;
  &__grid{
    @include grid348;
    margin-top: 2.5em;
  }
}

.othodontist-card{
  height: 100%;
  border-radius: .75em;
  background: rgba(255, 255, 255, 0.9);
  &__img{
    padding-bottom: 79%;
    @include coverImg;
    img{
      border-radius: .75em .75em 0 0;
    }
  }
  &__content{
    padding: 1.5em 2.5em 2.5em;
    @include grid100gap(.625em);
  }
  .content-text{
    ul{
      padding-left: 0;
      gap: 1em;
      li{
        &:before{
          background-color: $primary;
        }
      }
    }
  }
}

.lead-othodontist{
  padding: 6.25em 0 3.125em;
}

.othodontist-card2{
  background: #FFFFFF;
  border: 1px solid $lightGray;
  box-shadow: none;
  .othodontist-card__img{
    padding-bottom: 72%;
  }
  .othodontist-card__content{
    padding-top: 2.5em;
    gap: 1.25em;
  }
}

.info-service{
  background-color: transparent;
  padding: 3.125em 0;
  .info__desc{
    max-width: 47em;
  }
  .info__img{
    width: 24em;
    top: -3.375em;
    right: -2.875em;
  }
  .m-ul2{
    padding-left: 1.25em;
    gap: 1.125em;
  }
}

.info-service2{
  .info__inner{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding-right: 6.25em;
  }
  &__prices{
    display: flex;
    white-space: nowrap;
  }
  &__price2{
    text-decoration: line-through;
    margin-left: .35em;
  }
  &__mav{
    display: flex;
    margin-top: 1.25em;
  }
  &__btn{
    @include bigBtn;
  }
}

.compare2{
  padding: 3.125em 0;
}

.compare-card{
  padding: 2.5em;
  background-color: $blockBg;
  border-radius: .75em;
  height: 100%;
  &__top{
    min-height: 6em;
  }
  &__text{
    @include grid100gap(.25em);
  }
  &__items{
    margin-top: 1.25em;
  }
  &__item{
    padding: .625em 0;
    border-top: 1px solid $lightGray;
    &:first-child{
      padding-top: 0;
      border: none;
    }
    &:last-child{
      padding-bottom: 0;
    }
  }
  &__prices{
    display: flex;
    align-items: center;
    margin-top: .25em;
    img{
      width: 1.125em;
      margin-right: .625em;
    }
  }
  &__rating{
    display: flex;
    margin-top: .25em;
    .rating-input__item{
      width: 1.375em;
      svg{
        width: 100%;
        height: auto;
      }
    }
  }
}

.price2{
  padding: 6.25em 0;
  background-color: $blockBg;
}

.price-card{
  padding: 2.5em;
  background: #FFFFFF;
  border-radius: .75em;
  height: 100%;
  &__top{
    @include grid100gap(.5em);
  }
  &__items{
    margin-top: 1.25em;
    @include grid100gap(.5em);
  }
  &__nav{
    display: flex;
    margin-top: 1.25em;
  }
  &__btn{
    @include bigBtn;
  }
}

.bracket{
  padding: 6.25em 0 1.5em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 26.75em) 23.75em;
    gap: 3.75em 3em;
  }
  &__left{
    @include card;
    background-color: $blockBg;
  }
  &__top{
    @include grid100gap(1.25em);
  }
  &__items{
    margin-top: 1.875em;
    @include grid100gap(1.875em);
  }
  &__item{
    display: flex;

  }
  &__num{
    width: 2.125em;
    height: 2.125em;
    border-radius: 50%;
    margin-right: .625em;
    background-color: $primary;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
  }
  &__content{
    @include grid100gap(.25em);
  }
  &__desc{
    @include grid100gap(1em);
    p{
      line-height: inherit;
    }
  }
  .doctor-consult{
    height: unset;
  }
}

.equip2{
  padding: 3.125em 0 6.25em;
}

.equip-card2{
  @include card;
  border: 1px solid $lightGray;
  background-color: #fff;
  height: 100%;
  &__icon{
    height: 3.75em;
    display: block;
    margin-bottom: 1.5em;
  }
  &__content,
  &__desc{
    @include grid100gap(.625em);;
    p{
      line-height: inherit;
    }
  }
}

.value2{
  padding: 6.25em 0;
  background-color: $blockBg;
  @media screen and (min-width: 577px){
    .swiper{
      &-wrapper{
        width: 100% !important;
      }
    }
  }
}

.value-card{
  &__img{
    height: 3.75em;
    img{
      height: 100%;
      width: auto;
    }
  }
  &__english{
    padding: 1.25em;
    background: #FFFFFF;
    border-radius: .625em;
    margin: 1.25em 0;
  }
}

.review3{
  padding: 6.25em 0 3.125em;
}

.review-card2{
  .hidden-text{
    height: 22.5em;
  }
}


@media screen and (max-width: 576px) {
  .service {
    margin-bottom: 1.875em;
    position: relative;
    overflow: hidden;
    &__inner {
      position: static;
      grid-template-columns: 100%;
      padding-bottom: 3.75em;
      gap: 0;
    }
    &__info {
      gap: .5em;
      max-width: 14.125em;
    }
    &__list,
    &__nav{
      margin-top: 1.5em;
    }
    &__btn{
      width: 100%;
    }
    &__img1{
      display: none;
    }
    &__img2{
      display: block;
    }
    &__img{
      bottom: unset;
      right: -1.5em;
      top: 1.875em;
      width: 9.6525em;
    }
  }

  .service-article{
    padding: 1.875em 0;
    .content-text{
      hr{
        margin: .75em 0;
      }
      &>ul{
        gap: .85em;
      }
      &>blockquote{
        margin-top: 4.5em;
      }
    }
  }

  .universities{
    padding: 0;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: .75em;
  }

  .university-card{
    margin: 0 !important;
    width: 100%;
  }

  .best{
    padding: 1.875em 0;
    &__grid{
      @include scrollCollection;
      margin-top: 1.5em;
    }
  }

  .best-card{
    width: 17.5em;
    flex-shrink: 0;
    padding: .875em;
    &:not(:last-child){
      margin-right: .75em;
    }
    &__top{
      gap: .25em;
    }
    &__images{
      gap: .625em;
    }
    &__logo{
      font-size: 1em;
    }
    &__text{
      margin-top: .625em;
      ul{
        gap: .83em;
      }
    }
    &__text2{
      padding-top: 1.25em;
      margin-top: 0;
    }
  }

  .intellectual{
    padding: 3.75em 0;
    margin-top: 1.875em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__subtitle{
      margin-top: 0;
      gap: 0;
    }
    &__desc{
      margin-top: .5em;
    }
    &__items{
      margin-top: 1.5em;
      grid-template-columns: 100%;
      gap: 1.25em;
    }
  }

  .intellectual-card{
    padding: 1.5em;
    &__num{
      margin-right: .625em;
    }
    &__text{
      margin-top: .75em;
      gap: .25em;
    }
  }

  .req2{
    padding: 0 0 1.875em;
    margin-bottom: 0;
    &__img{
      position: static;
      width: calc(100% + 3em);
      height: 15.625em;
      margin-left: -1.5em;
      object-fit: cover;
    }
    .req{
      margin: -6.875em 0 0;
      width: 100%;
    }
  }

  .m-slider{
    &__top{
      gap: .625em;
    }
    .swiper{
      &-slide{
        width: 15.75em;
        padding-right: .75em;
      }
    }
  }

  .algorithm{
    padding: 1.875em 0 3.75em;
  }

  .algorithm-card{
    padding: .875em;
    &__top,
    &__what-do{
      gap: 0;
    }
    &__badge{
      margin: .625em 0;
      padding: 0 .5em;
      height: 1.8125em;
    }
    &__nav{
      margin-top: .625em;
    }
  }

  .othodontist{
    padding: 3.75em 0;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }

  .othodontist-card{
    &__img{
      padding-bottom: 65%;
    }
    &__content{
      padding: .875em 1.5em 1.5em;
    }
  }

  .lead-othodontist{
    padding: 3.75em 0;
    .swiper-slide{
      width: 19.5em;
    }
  }

  .othodontist-card2{
    .othodontist-card__img{
      padding-bottom: 65%;
    }
    .othodontist-card__content{
      padding-top: .625em;
      gap: .5em;
    }
  }

  .info-service{
    padding: 1.875em 0;
    .info__img{
      width: 8.94em;
      top:  0;
      right: -3.44em;
    }
  }

  .info-service2{
    .info__right{
      margin-top: 1.25em;
    }
    .info__inner{
      display: block;
      padding-right: 1.5em;
    }
    .info__desc{
      padding-right: 0;
    }
    &__btn{
      width: 100%;
    }
  }

  .compare2{
    padding: 1.875em 0;
    .swiper-slide{
      width: 18.25em;
    }
  }

  .compare-card{
    padding: .875em .875em 1.5em;
    &__top{
      min-height: unset;
      .display4{
        min-height: 2.44em;
        display: flex;
        align-items: center;
      }
    }
  }

  .price2{
    padding: 1.875em 0;
    .swiper-slide{
      width: 19.5em;
    }
  }

  .price-card{
    padding: 1.5em;
    &__btn{
      font-size: .875em;
      padding: 0 1em;
      height: 2.86em;
    }
  }

  .bracket{
    padding: 3.75em 0 1.875em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__right{
      order: -1;
    }
    &__left{
      padding: 1.5em;
    }
    &__top{
      gap: .625em;
    }
    &__items{
      margin-top: 1.25em;
      gap: 1.25em;
    }
  }

  .equip2{
    padding: 1.875em 0 3.75em;
    .swiper-slide{
      width: 17em;
    }
  }

  .equip-card2{
    padding: .875em;
    &__icon{
      margin-bottom: .875em;
      height: 2.5em;
    }
    &__content{
      gap: .3125em;
    }
  }

  .value2{
    padding: 3.75em 0;
    .swiper-slide{
      width: 16.125em;
      padding: 1.25em 3em 0 0;
    }
    .m-slider__slider{
      margin-top: 0;
    }
  }

  .value-card{
    &__img{
      height: 2.5em;
    }
    &__english{
      padding: .625em;
      margin: .625em 0;
    }
  }

  .review3{
    padding: 3.75em 0 1.875em;
    .swiper-slide{
      width: 19em;
      padding-right: 1.5em;
    }
  }
}
