@import "../mixins";
.price--tutor{
  .tv-header__back{
    background: $light;
  }
}

.tutors{
  padding-bottom: 3.125em;
  .how-recover__slider{
    margin: 0;
  }
}

.tutor-card{
  &__info{
    margin-top: .875em;
    @include grid100gap(.875em);
  }
  &__contacts{
    @include grid100gap(0em);
  }
}

@media screen and (max-width: 576px){
  .tutors{
    padding: .75em 0 1.875em;
  }
}
