.tv-header{
  background-color: $blockBg;
  margin-bottom: 3.125em;
  &__inner{
    padding: 1em 0 6.25em;
  }
  &__info{
    margin-top: 3em;
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
    max-width: 48.5em;
  }
  &__back{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 1.75em;
    height: 1.75em;
    box-shadow: none;
    img{
      width: .75em;
      display: block;
      transition: all .3s;
    }
    &:hover{
      img{
        opacity: .8;
      }
    }
  }
}

.when-come{
  padding: 3.125em 0 6.25em;
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
  }
  &__grid{
    margin-top: 2.5em;
    padding-left: 12.25em;
    position: relative;
  }
  &__items{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.5em;
    width: 100%;
    max-width: 38.4375em;
    padding-left: 1.5em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: .4375em;
      height: 100%;
      background: linear-gradient(180deg, #9145C0 0%, #CE9FED 100%);
      border-radius: 2.5em;
    }
  }
  &__level{
    position: absolute;
    width: 10.75em;
    text-align: right;
    left: 0;
  }
  &__level1{
    top: 0;
  }
  &__level2{
    bottom: 0;
  }
}

.when-come-card{
  padding: 1.25em 3.125em;
  background-color: $blockBg;
  border-radius: .5em;
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: .25em;
  }
  &__title{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      border-radius: .0625em;
      background-color: $primary;
      width: .25em;
      height: .25em;
      right: 100%;
      top: .625em;
      margin-right: .75em;
      transform: translateY(-50%);
    }
  }
  &__time,
  &__link{
    display: flex;
    align-items: center;
    img{
      width: .625em;
      flex-shrink: 0;
      margin-right: .375em;
      display: block;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    margin-top: 1.5em;
  }
  &__link{
    img{
      width: 1em;
    }
  }
}

.sale-doctors--tv{
  padding: 6.25em 0 3.125em;
  background-color: $blockBg;
}

.when-need{
  padding: 3.125em 0 6.25em;
  background-color: $blockBg;
  margin-bottom: 1.625em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5em));
    gap: 3em;
  }
  &__left{
    padding: 2.5em;
    background: #FFFFFF;
    border-radius: .75em;
  }
  &__items{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
    margin-top: 1.875em;
  }
  &__item{
    position: relative;
    padding-left: 1.875em;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 1.25em;
      height: 1.25em;
      background-image: url(../img/check4.svg);
      background-size: 100% auto;
      background-position: center;
      background-repeat: no-repeat;
    }
  }
  &__right{
    padding: 2.5em;
    background: radial-gradient(62.23% 670.3% at 91.8% 13.33%, #E8E2DA 0%, #E7E1D9 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
    box-shadow: 0px .25em 1.25em rgba(119, 110, 112, 0.1);
    border-radius: .75em;
  }
}


@media screen and (max-width: 576px){
  .tv-header{
    margin-bottom: 1.875em;
    &__inner{
      padding: 1.5em 0 3.75em;
    }
    &__info{
      margin-top: 1.5em;
      gap: .625em;
    }
    &__subtitle{
      margin-bottom: 1.15em;
    }
  }

  .when-come{
    padding: 1.875em 0 3.75em;
    &__info{
      gap: .5em;
    }
    &__grid{
      padding: 0;
      display: grid;
      grid-template-columns: 100%;
      gap: .75em;
      margin-top: 1.5em;
    }
    &__items{
      padding-left: 1.0625em;
      gap: 1.25em;
    }
    &__level{
      position: static;
      width: unset;
      text-align: left;
    }
  }

  .when-come-card{
    padding: .6875em 2.25em;
    &__title{
      &:before{
        top: .5em;
      }
    }
    &__nav{
      margin-top: .75em;
    }
  }

  .sale-doctors--tv{
    padding: 3.75em 0 1.875em;
  }

  .when-need{
    padding: 1.875em 0 3.75em;
    margin-bottom: 1.875em;
    &__grid{
      grid-template-columns: 100%;
      gap: 3.75em;
    }
    &__left,
    &__right{
      padding: 1.5em;
    }
    &__items{
      gap: .625em;
      margin-top: .625em;
    }
    .tv-med2__step-num{
      background-color: $primary;
      color: #fff;
    }
  }
}
