@import "../mixins";
.contacts{
  background-color: $blockBg;
  &__inner{
    padding: 1em 0 6.25em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5em));
    gap: 3.75em 3em;
  }
  &__info{
    @include grid100gap(1.25em);
  }
  &__info2,
  &__info3{
    @include grid100gap(.625em);
    margin-top: 3em;
  }
  &__time{
    @include grid100gap(.1875em);
  }
  .footer__socials{
    padding: 0;
  }
  .footer__social{
    width: 3.375em;
    height: 3.375em;
    background: rgba(255, 255, 255, 0.8);
    box-shadow: none;
    img{
      width: 1.5em;
    }
    &:hover{
      //background: rgba(255, 255, 255, 0.6);
    }
  }
  &__items{
    @include grid100gap(3em);
  }
}

.clinic-card{
  @include card;
  background-color: #fff;
  display: grid;
  grid-template-columns: calc(100% - 15.6875em) 13.1875em;
  gap: 2.5em;
  position: relative;
  &__info{
    @include grid100gap(.625em);
    padding-top: .625em;
  }
  &__nav{
    display: flex;
    margin-top: 1.25em;
  }
}

.map{
  border-radius: .5em;
  overflow: hidden;
  width: 100%;
  height: 100%;
  position: relative;
  iframe{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  .zoom-control{
    opacity: 0 !important;
  }
}

.docs--contacts{
  .tax-info{
    margin-top: 0;
    display: flex;
    flex-direction: column;
    &__nav{
      padding-top: .625em;
      margin-top: auto;
    }
  }
}

.contacts2{
  padding: 6.25em 0 3.125em;
  &__inner{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5em));
    gap: 3.75em 3em;
  }
  @media screen and (min-width: 577px){
    .article-stage__form{
      padding-top: 5em;
    }
  }
}

.personal-tutor{
  @include card;
  background-color: #fff;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 13.75em) 12.5em;
    gap: 1.25em;
  }
  &__images{
    display: flex;
    justify-content: flex-end;
  }
  &__desc{
    grid-column: 1/-1;
  }
  .selection-card__images{
    margin: 0;
  }
  &__nav{
    display: flex;
    margin-top: 1.875em;
  }
}

.feedback2{
  padding: 3.125em 0;
  .req{
    max-width: 52.3125em;
    margin: 0;
  }
}

.equip--contacts,
.how-recover--contacts{
  background-color: transparent;
}

.how-recover--contacts{
  padding-bottom: 3.125em;
}

.road{
  padding: 4.75em 0 3.125em;
  &__tabs{
    margin-top: .75em;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5em));
    gap: 3em;
    margin-top: 3em;
  }
  @media screen and (min-width: 577px){
    &__tabs{
      display: none;
    }
    ._tab-content{
      display: block;
    }
  }
}

.road-card{
  @include card;
  border: 1px solid $lightGray;
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
  }
  &__items{
    margin-top: 1.25em;
    @include grid100gap(.125em);
  }
  &__item{
    display: flex;
    &:last-child{
      .road-card__left{
        &:before{
          display: none;
        }
      }
    }
  }
  &__left{
    flex-shrink: 0;
    margin-right: .75em;
    position: relative;
    padding-top: .1875em;
    &:before{
      content: '';
      position: absolute;
      width: 2px;
      height: calc(100% - 2.125em);
      bottom: 0;
      left: 50%;
      -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
      transform: translateX(-50%);
      border-left: 2px dashed #e7e1d9;
    }
  }
  &__icon{
    width: 1.625em;
    height: 1.625em;
    border-radius: 50%;
    background: $blockBg;
    backdrop-filter: blur(7px);
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      width: .75em;
      height: auto;
    }
  }
  &__content{
    padding: .1875em 0 .625em;
    @include grid100gap(.625em);
  }
  &__info{
    min-height: 1.625em;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}

.clinic{
  padding-bottom: 4.75em;
  background-color: $blockBg;
  &__inner{
    margin-top: 3em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5em));
    gap: 1.25em 3em;
  }
  &__info{
    @include grid100gap(.625em)
  }
  &__desc{
    margin: .625em 0 2.375em;
  }
  &__list{
    gap: .5em;
  }
  &__nav{
    display: flex;
    margin-top: 3em;
  }
  &__btn{
    @include bigBtn;
  }
  &__map{
    height: 15.9375em;
    margin-top: 1.875em;
  }
  &__way{
    margin-top: 2.5em;
    @include grid100gap(1.25em);
    &-items{
      display: flex;
    }
    &-btn{
      &:not(:last-child){
        margin-right: 1.5em;
      }
      img{
        width: 1.29em;
        margin-right: .67em;
      }
    }
  }
}

.parking{
  @include grid100gap(.625em);
  margin-top: 2.5em;
  &__title{
    display: flex;
    align-items: center;
  }
  &__icon{
    width: 1.25em;
    flex-shrink: 0;
    margin-right: .625em;
    img{
      width: 100%;
    }
  }
  &__list{
    margin-top: .625em;
    gap: .625em;
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__inner{
      padding: 2.25em 0 13.875em;
      grid-template-columns: 100%;
      position: relative;
    }
    &__info{
      gap: .625em;
    }
    &__info2{
      margin-top: 3.75em;
      gap: .75em;
    }
    &__info3{
      position: absolute;
      gap: 1.5em;
      bottom: 3.75em;
      width: 100%;
    }
    &__items{
      gap: 1.5em;
    }
  }

  .clinic-card{
    padding: 1.5em;
    grid-template-columns: calc(100% - 7.75em - .625em) 7.75em;
    gap: .625em;
  }

  .contacts2 {
    padding: 5em 0 1.875em;
    &__inner {
      grid-template-columns: 100%;
    }
  }

  .personal-tutor{
    padding: 1.5em;
    &__grid{
      grid-template-columns: 100%;
      gap: .5em;
    }
    &__images{
      order: 2;
      justify-content: unset;
      margin-top: .75em;
    }
  }

  .feedback2{
    padding: 1.875em 0;
  }

  .road{
    padding: 3.75em 0 1.875em;
    &__grid{
      margin-top: .625em;
      grid-template-columns: 100%;
    }
  }

  .road-card{
    padding: .875em;
    &__title{
      display: none;
    }
    &__grid{
      margin-top: 0;
      grid-template-columns: 100%;
      gap: 1.875em;
    }
  }

  .clinic{
    padding: 1em 0 3.75em;
    &__inner{
      grid-template-columns: 100%;
      margin-top: 1.5em;
    }
    &__desc{
      margin: 0 0 .875em;
    }
    &__nav{
      margin-top: 1.5em;
    }
    &__btn{
      width: 100%;
    }
    .tv__tabs{
      margin: 0;
    }
    &__map{
      margin: 1.25em 0 0 -1.5em;
      width: calc(100% + 3em);
      height: 10.1875em;
      &-info{
        background: rgba(255, 255, 255, 0.8);
        backdrop-filter: blur(7px);
        border-radius: .75em;
        padding: 1.25em;
        position: relative;
        z-index: 3;
        margin-top: -1.25em;
      }
      .map{
        border-radius: 0;
      }
    }
    &__way{
      margin: 0;
      gap: .625em;
      &-items{
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: .75em;
      }
      &-btn{
        margin: 0 !important;
      }
    }
  }

  .parking{
    margin-top: 1.5em;
    &__list{
      margin-top: .2em;
    }
  }
}
