@import "../mixins";

.memos{
  margin-bottom: 1.5em;
  &__inner{
    display: flex;
    padding: 1em 0;
  }
  &__info{
    @include grid100;
    gap: 4.75em;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(6,calc(100% / 6 - (5em / 6)));
    gap: 1em;
    margin-top: 2.5em;
  }
}

.memo-card{
  padding: 1.5em;
  background-color: #FFFFFF;
  background-repeat: no-repeat;
  background-size: 5.4375em auto;
  border-radius: .75em;
  &__nav{
    margin-top: .875em;
    display: flex;
  }
  &__btn{
    img{
      margin-right: .57em;
      width: 1.29em;
      display: block;
      flex-shrink: 0;
    }
  }
}

.memos-video{
  padding: 4.75em 0 6.25em;
  &__info{
    @include grid100;
    gap: 1.25em;
  }
  &__grid{
    @include grid348;
    margin-top: 2.5em;
  }
}

.memo-card2{
  &__img{
    display: block;
    position: relative;
    padding-bottom: 65%;
    margin-bottom: 1em;
    &>img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: .75em;
    }
  }
  .doctor-card2__play{
    left: 50%;
    bottom: 50%;
    transform: translate(-50%,50%);
    z-index: 2;
  }
}

.subscribe-service{
  padding: 6.25em 0;
  background-color: $blockBg;
  &__info{
    @include grid100;
    gap: 1.25em;
  }
  &__links{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 1.25em 2.5em;
    margin-top: 2.5em;
    &-col{
      @include grid100;
      gap: 1.25em;
    }
  }
  &__link{
    color: $linkBlue;
  }
}


@media screen and (max-width: 576px){
  .memos{
    &__inner{
      padding-bottom: .375em;
    }
    &__info{
      gap: 3em;
    }
    &__grid{
      @include scrollCollection;
      padding-top: 1.5em;
      padding-bottom: 1.5em;
      margin-top: 0;
    }
  }

  .memo-card{
    width: 11.25em;
    flex-shrink: 0;
    &:not(:last-child){
      margin-right: .75em;
    }
  }

  .memos-video{
    padding: 1.875em 0;
    &__info{
      gap: .625em;
    }
    &__grid{
      margin-top: 1.5em;
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }

    .memo-card2{
      &__img{
        margin-bottom: .375em;
      }
    }
  }

  .subscribe-service{
    padding: 3em 0;
    &__info{
      gap: .625em;
    }
    &__links{
      @include scrollCollection;
      padding-top: 1.5em;
      padding-bottom: 1.5em;
      margin-top: 0;
      &-col{
        width: 13.125em;
        flex-shrink: 0;
        &:not(:last-child){
          margin-right: 1em;
        }
      }
    }
  }
}
