@import "../mixins";
.about2{
  &__inner{
    padding: 1em 0 1.5em;
  }
  &__info{
    max-width: 48.5em;
    margin-top: 3em;
    @include grid100gap(1.25em);
  }
}

.co-history{
  padding: 1.5em 0 3.125em;
  &__items{
    @include grid100gap(.75em);
  }
}

.event-card{
  display: grid;
  grid-template-columns: 18em auto;
  gap: 3em;
  &:last-child{
    .event-card__num:before{
      display: none;
    }
  }
  &__num{
    text-align: center;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 2px;
      height: calc(100% - 4.75em);
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      border-left: 2px dashed $lightGray;
    }
  }
  &__content{
    padding: 1.25em 0 2.5em;
  }
  &__info{
    @include grid100gap(1.25em);
  }
}


@media screen and (max-width: 576px){
  .about2 {
    &__inner {
      padding-bottom: .75em;
    }
    &__info{
      margin-top: 2.25em;
      gap: .625em;
    }
  }

  .co-history{
    padding: 3.375em 0 1.875em;
    &__items{
      gap: .625em;
    }
  }

  .event-card{
    display: flex;
    gap: unset;
    &__num{
      flex-shrink: 0;
      margin-right: .875em;
      &:before{
        height: calc(100% - 2.5625em);
      }
    }
    &__content{
      padding: .375em 0 .875em;
    }
    &__info{
      gap: .5em;
    }
  }
}
