.tv-header--plans{
  .tv-header__inner{
    padding-bottom: 4.75em;
  }
}

.tv-products{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 2em));
  gap: 3em;
  margin-top: 2.5em;
}

.product-card2{
  background-color: #fff;
  border-radius: .75em;
  padding: .875em 1.5em 1.5em;
  display: flex;
  flex-direction: column;
  &__img{
    display: flex;
    justify-content: center;
    height: 8.5em;
    img{
      height: 100%;
    }
  }
  &__info{
    margin-top: .375em;
    display: grid;
    grid-template-columns: 100%;
    gap: .375em;
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: auto;
    padding-top: 1.25em;
  }
  &__btn{
    img{
      width: .9em;
      display: block;
      flex-shrink: 0;
      margin-right: .4em;
    }
  }
}

.doctors-subscribe--white{
  background-color: transparent;
  padding: 3.125em 0;
}

.tech{
  padding: 3.125em 0;
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
  }
  &__slider{
    margin-top: 2.5em;
    .swiper{
      @media screen and (min-width: 577px){
        &-wrapper{
          display: grid;
          grid-template-columns: repeat(3,calc(100% / 3 - 2em));
          gap: 3em;
          transform: unset !important;
        }
        &-slide{
          width: 100% !important;
        }
      }
    }
  }
}

.tech-card{
    &__img{
      position: relative;
      padding-bottom: 45%;
      margin-bottom: 1.25em;
      img{
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        object-fit: cover;
        border-radius: .75em;
      }
    }
}


@media screen and (max-width: 576px){
  .tv-header--plans{
    .tv-header__inner{
      padding-bottom: 3.75em;
    }
  }

  .product-card2{
    &__info{
      margin-top: 1.75em;
    }
  }

  .tv-products{
    grid-template-columns: 100%;
    gap: 1.5em;
  }

  .doctors-subscribe--white{
    padding: 1.875em 0;
  }

  .tech{
    padding: 1.875em 0;
    &__slider{
      margin-top: 1.5em;
      .swiper{
        &-slide{
          width: 12em;
          padding-right: .75em;
        }
      }
    }
  }

  .tech-card{
    &__img{
      padding-bottom: 70%;
      margin-bottom: .625em;
    }
  }
}
