@import "../mixins";
.installment{
  margin-bottom: 1.5em;
  background-color: $blockBg;
  &__inner{
    display: flex;
    padding: 1em 0 6.25em;
  }
  &__left{
    flex-grow: 1;
  }
  &__right{
    width: 31.25em;
    flex-shrink: 0;
    margin-left: 3em;
  }
  &__title{
    @media screen and (min-width: 577px){
      font-size: 2em;
      padding-right: .5em;
      padding-top: .28em;
      span{
        display: block;
        &:before{
          width: .84em;
          height: .84em;
          top: -.28em;
        }
      }
    }
  }
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
  }
  &__list{
    gap: .5em;
    margin-top: 1.55em;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5em));
    gap: 3em;
    margin-top: 3em;
  }
  &__img{
    width: 100%;
    display: block;
  }
  &__tariffs{
    display: grid;
    grid-template-columns: repeat(3,calc((100% / 3) - (2.5em / 3)));
    gap: 1.25em;
  }
}

.installment-info{
  .prices-service__right{
    padding: 0;
  }
}

.installment-tariff{
  &__inner{
    background: rgba(255, 255, 255, 0.9);
    border: 1px solid #E7E1D9;
    border-radius: .75em;
    padding: 2.5em;
  }
  &__full{
    margin-top: 1.25em;
    padding: .8125em 1.25em;
    background-color: $blockBg;
    border-radius: .75em;
    display: grid;
    grid-template-columns: 100%;
    gap: .625em;
  }
}

.req,
.tax-info{
  padding: 2.5em;
  background-color: $blockBg;
  margin-top: 3em;
  border-radius: .75em;
}

.req{
  &__info,
  &__fields{
    display: grid;
    grid-template-columns: 100%;
    gap: .625em;
  }
  &__fields{
    margin-top: 1.25em;
    gap: 1.25em;
  }
  &__nav{
    margin: 1.25em 0 .625em;
    display: flex;
  }
}

.tax-info{
  &__text{
    margin-top: 1.25em;
    ol{
      padding-left: 0;
    }
  }
  &__nav{
    margin: 1.25em 0 0;
    display: flex;
    .m-btn-light{
      background-color: #FFFFFF;
    }
  }
  &__btn{
    &:not(:last-child){
      margin-right: 1.42em;
    }
  }
}


@media screen and (max-width: 576px){
  .installment{
    margin-bottom: 1.875em;
    &__inner{
      display: block;
      padding-bottom: 3.75em;
    }
    &__right{
      display: none;
    }
    &__title{
      padding: 0;
      span{
        &:before{
          display: none;
        }
      }
    }
    &__info{
      gap: .625em;
    }
    &__list{
      margin-top: .875em;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .375em));
      gap: .75em;
      margin-top: 1.5em;
    }
    .tv-nums{
      &__item{
        width: 100%;
        margin: 0 !important;
      }
      &__title{
        font-size: 1.125em;
      }
    }
    &__tariffs{
      display: flex;
      gap: unset;
      width: calc(100% + 3em);
      margin-left: -1.5em;
      padding: 0 1.5em;
      overflow-x: auto;
      @include scroll0;
    }
  }

  .installment-tariff{
    width: 17.5em;
    flex-shrink: 0;
    &:not(:last-child){
      margin-right: 1.25em;
    }
    &__inner{
      padding: .875em;
    }
    &__full{
      margin-top: .875em;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: unset;
      .text18{
        white-space: nowrap;
        margin-left: 1em;
      }
    }
  }

  .installment-info{
    .prices-service__right{
      order: unset;
    }
  }

  .req{
    padding: 1.5em;
  }

  .tax-info{
    padding: 1.5em;
    &__text,
    &__nav{
      margin-top: .625em;
    }
  }
}
