@import "../mixins";

.service-rating{
  .article__rating{
    margin: 0;
  }
  &__wrap{
    margin-top: 3em;
    display: flex;
    align-items: center;
  }
  &__left{
    margin-right: 3.75em;
  }
  &__time{
    @include grid100gap(.25em);
  }
}

.service-product{
  padding: 6.25em 0;
  background-color: $blockBg;
}

.product-card3{
  display: grid;
  grid-template-columns: repeat(2,50%);
  background-color: #fff;
  border-radius: .75em;
  position: relative;
  box-shadow: 0 .25em 1.5em rgba(119,110,112,.1);
  &__left,
  &__right{
    padding: 3.75em;
  }
  &__img{
    height: 20.8125em;
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__info{
    @include grid100gap(.625em);
  }
  &__items{
    margin-top: 1.5em;
    @include grid100gap(.625em);
  }
  &__item{
    display: flex;
    align-items: center;
    &-icon{
      width: 1.125em;
      display: block;
      flex-shrink: 0;
      margin-right: .75em;
      img{
        width: 100%;
      }
    }
  }
  &__price{
    margin-top: 3.25em;
  }
  &__nav{
    display: flex;
    margin-top: 1.25em;
  }
}

.service-detail{
  padding: 4.75em 0 6.25em;
  &__groups{
    @include grid100gap(6.25em);
  }
  &__group{
    display: grid;
    grid-template-columns: auto 24.6875em;
    gap: 3.75em 3em;
  }
  &__left{
    max-width: 48.5em;
  }
  .content-text{
    h3{
      font-size: 1.25em;
    }
  }
  .memo-card2{
    &__title{
      font-size: 1.375em;
    }
  }
  &__info{
    @include grid100gap(1.25em);
  }
  &__compare{
    @include grid100gap(.625em);
    img{
      width: 100%;
      display: block;
    }
  }
}

.doctor-review{
  padding: 6.25em 0;
  background-color: $blockBg;
  background-image: url(../img/review-bg.svg);
  background-size: auto 100%;
  background-repeat: no-repeat;
  background-position: right top;
  &__info{
    @include grid100gap(2.5em);
    max-width: 48.5em;
  }
}

.rating-service{
  padding: 6.25em 0 2.5em;
  .m-slider__top{
    max-width: 48.5em;
  }
}

.review4{
  padding: 2.5em 0 6.25em;
  .m-slider__slider{
    margin: 0;
  }
}

.service-advantages{
  padding: 6.25em 0;
  background-color: $blockBg;
  &__top{
    max-width: 48.5em;
  }
  &__grid{
    margin-top: 3em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5em));
    gap: 1.25em 3em;
  }
  &__group{
    background-color: #fff;
    @include card;
    @include grid100gap(1.5em)
  }
  &__items{
    li{
      &:before{
        background-color: transparent;
        background-size: 100% auto;
        background-image: url(../img/check4.svg);
        width: 1.25em;
        height: 1.25em;
        margin-right: .625em;
        transform: translateY(.1em);
      }
    }
    &.uncheck-list{
      li{
        &:before{
          background-image: url(../img/uncheck.svg);
        }
      }
    }
  }
}

.compare3{
  padding: 6.25em 0 3.125em;
  &__group{
    @include card;
    background-color: $blockBg;
  }
  &__info{
    @include grid100gap(1.25em);
  }
}

.how-consult--service{
  @media screen and (min-width: 577px){
    .how-consult__grid{
      grid-template-rows: repeat(5,auto);
    }
  }
}

.service-price{
  padding: 3.125em 0 6.25em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5em));
    gap: .75em 3em;
    margin-top: 2.5em;
  }
}

.compare-price{
  padding: 6.25em 0;
  background-color: $blockBg;
  &__grid{
    margin-top: 3em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5em));
    gap: 1.25em 3em;
  }
}

.compare-card2{
  background-color: #fff;
  @include card;
  &__top{
    @include grid100gap(1.25em);
    .m-ul2{
      gap: .5em;
    }
  }
  &__consult{
    @include grid100gap(.25em);
    margin-top: 2em;
    &-grid{
      display: grid;
      grid-template-columns: 44% auto 44%;
      align-items: center;
    }
    &-left{
      display: flex;
      flex-direction: column-reverse;
      justify-content: center;
    }
    &-right{
      padding: .625em 1.25em;
      background: #F7F0FB;
      border-radius: .5em;
    }
  }
  &__or{
    text-align: center;
  }
  &__item{
    padding: .75em 0;
    border-bottom: 1px solid $lightGray;
    &:last-child{
      border: none;
      padding-bottom: 0;
    }
  }
  &__groups{
    @include grid100gap(.875em);
    margin-top: .875em;
  }
  .spoiler-card{
    margin-top: 1.875em;
    border: none;
    &__show{
      padding: 0;
    }
    &__hidden{
      padding: 0;
    }
  }
  &__nav{
    display: flex;
    margin-top: 2em;
  }
  &__btn{
    img{
      display: block;
      width: 1.125em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
}

.answers{
  padding: 6.25em 0 3.125em;
  &__inner{
    @include card;
    background-color: $blockBg;
  }
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 1.25em 2.5em;
  }
  .memo-card2{
    &__img{
      padding-bottom: 44%;
    }
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
  }
  &__items{
    @include grid100gap(1.25em);
  }
}

.answer-card{
  display: flex;
  align-items: center;
  padding: .875em 1.5em;
  background: #FFFFFF;
  border-radius: .75em;
  &__icon{
    width: 2.625em;
    flex-shrink: 0;
    display: block;
    margin-right: 1.25em;
    img{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .service-rating{
    &__wrap{
      display: block;
    }
    &__left{
      margin: 0 0 .625em;
    }
    &__time{
      gap: 0;
    }
    .author{
      max-width: 16.875em;
    }
  }

  .service-product{
    padding: 2.5em 0 3.75em;
  }

  .product-card3{
    display: block;
    padding: 2.375em 1.5em 1.5em;
    &__left,
    &__right{
      padding: 0;
    }
    &__img{
      height: 8.5em;
    }
    &__info{
      margin-top: 1.75em;
    }
    &__items{
      margin-top: 1.25em;
    }
    &__item{
      &-icon{
        margin-right: .5em;
      }
    }
    &__price,
    &__nav{
      margin: 0;
    }
    &__bottom{
      margin-top: 1.5em;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }

  .service-detail{
    padding: 3.75em 0;
    &__groups{
      gap: 3.75em;
    }
    &__group{
      grid-template-columns: 100%;
    }
    .mob-order1{
      order: -1;
    }
    .memo-card2{
      display: flex;
      flex-direction: column-reverse;
      &__img{
        margin: 1.5em 0 0;
      }
      &__title{
        font-size: 1.5em;
        font-weight: 400;
      }
    }
    &__info{
      gap: 1.5em;
    }
  }

  .doctor-review{
    padding: 3.75em 0;
    background-image: none;
    &__info{
      gap: 1.5em;
    }
  }

  .rating-service{
    padding: 3.75em 0 1.875em;
  }

  .review4{
    padding: 1.875em 0 3.75em;
  }

  .service-advantages{
    padding: 3.75em 0;
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1.5em;
    }
    &__group{
      padding: 1.5em;
      gap: .625em;
    }
  }

  .compare3{
    padding: 3.75em 0 1.875em;
    &__group{
      padding: .875em;
      width: 15em;
      flex-shrink: 0;
      &:not(:last-child){
        margin-right: .625em;
      }
    }
    &__info{
      gap: .675em;
    }
    .service-advantages__grid{
      @include scrollCollection
    }
  }

  .service-price{
    padding: 1.875em 0 3.75em;
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1.5em;
    }
  }

  .compare-price{
    padding: 3.75em 0;
    background-color: $blockBg;
    &__grid{
      @include scrollCollection;
      margin-top: 1.5em;
    }
  }

  .compare-card2{
    width: 18.25em;
    padding: .875em;
    flex-shrink: 0;
    &:not(:last-child){
      margin-right: .75em;
    }
    &__top{
      gap: .625em;
      .m-ul2{
        gap: 0;
      }
    }
    &__consult{
      margin-top: 1.125em;
      &-right{
        padding: .625em;
      }
    }
    &__item{
      padding: .625em 0;
    }
    &__nav{
      margin-top: 1.25em;
    }
    &__btn{
      width: 100%;
    }
  }

  .answers{
    padding: 1.875em 0;
    &__inner{
      padding: 1.5em 1.5em 5.25em;
      position: relative;
    }
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1.25em;
    }
    .memo-card2{
      &__img{
        padding-bottom: 54%;
      }
    }
    &__items{
      gap: .625em;
    }
    &__nav{
      position: absolute;
      bottom: 0;
      left: 0;
      margin: 0;
      width: 100%;
      padding: 1.5em;
    }
    &__btn{
      width: 100%;
    }
  }
}
