.subscribe{
  padding: 3.125em 0 6.25em;
  &__info{
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
    max-width: 48.5em;
  }
  &__list{
    margin-top: 1.5em;
    gap: .75em;
    &:first-child{
      margin-top: 0;
    }
  }
  &__price{
    display: grid;
    grid-template-columns: 100%;
    gap: .375em;
    margin-top: 3em;
  }
}

.doctors-subscribe{
  padding: 6.25em 0;
  margin-bottom: 3.125em;
  background-color: $blockBg;
  &__bottom{
    margin-top: 3em;
    display: flex;
    justify-content: space-between;
  }
  &__btn{
    height: 3.4em;
    width: 14.45em;
    img{
      width: 1.2em;
      display: block;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
  &__spoiler{
    flex-grow: 1;
    max-width: 52.5em;
  }
  .result-card__text{
    margin: 0;
  }
}


@media screen and (max-width: 576px){
  .subscribe{
    padding: 1.875em 0 3.75em;
    &__info{
      gap: .5em;
    }
    &__list{
      margin-top: 1em;
      gap: .625em;
    }
    &__price{
      margin-top: 2.25em;
    }
  }

  .doctors-subscribe{
    padding: 3.75em 0;
    margin-bottom: 1.875em;
    &__bottom{
      margin-top: 3em;
      display: grid;
      grid-template-columns: 100%;
      gap: 3em;
    }
    &__btn{
      width: 100%;
    }
  }
}
