@import "vars";
@mixin font-face($name, $file, $weight: 400, $style: normal) {
    @font-face {
        font-family: "#{$name}";
        src: url('../fonts/#{$file}.ttf') format('ttf'),
        url('../fonts/#{$file}.woff2') format('woff2'),
        url('../fonts/#{$file}.woff') format('woff'),
        url('../fonts/#{$file}.otf') format('otf');
        font-weight: $weight;
        font-style: $style;
        font-display: swap;
    }
}

@mixin scroll0 {
    &::-webkit-scrollbar {
        width: 0;
        height: 9px;
    }
}

@mixin scroll {
    &::-webkit-scrollbar {
        width: 3px;
    }
    &::-webkit-scrollbar-track {
        background: $yellow;
        border-radius: 3px;
    }
    &::-webkit-scrollbar-thumb {
        background: $black;
        border-radius: 3px;
    }
}

@mixin scrollCollection {
    display: flex;
    gap: unset;
    width: calc(100% + 3em);
    margin-left: -1.5em;
    padding: 0 1.5em;
    overflow-x: auto;
    @include scroll0;
}

@mixin grid100 {
    display: grid;
    grid-template-columns: 100%;
}

@mixin grid100gap($gap) {
    display: grid;
    grid-template-columns: 100%;
    gap: $gap;
}

@mixin grid348 {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 2em));
    gap: 3em;
}

@mixin coverImg {
    position: relative;
    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        position: absolute;
        top: 0;
        left: 0;
    }
}


@mixin bigBtn{
    height: 3.4em;
    img{
        width: 1.2em;
        display: block;
        flex-shrink: 0;
        margin-right: .5em;
    }
}


@mixin card{
    padding: 2.5em;
    border-radius: .75em;
}
